import { AssignableInfo, randomIntFromInterval, validEffort, validIterations } from "./cards.provider/assignable";
import { useState } from 'react';
import { Home, HomeWork, LocationOn, NextWeek, PinDrop, Timelapse } from "@material-ui/icons";
import { getActions } from './cards.actions/actions.presets'
import { getCurrentUserRole } from './cards.actions'
import { counterTinder } from "./TinderCards";
const { SingleTermLabel } = require('@targetprocess/entity-label')

interface CardProps {
  info: any
}

interface ActionProp {
  direction: 'left' | 'right'
}

const Action = (prop: ActionProp) => {
  const actions = getActions(getCurrentUserRole())
  const text = prop.direction === 'left' ? actions[1] : actions[0]
  return <div
    className={`action ${prop.direction}`}
  >
    {text}
  </div>
}


// id
// name
// role
// location
// age
// avatar
export const AssignableCard = (props: CardProps) => {
  const { info } = props
  const [isPressed, setIsPressed] = useState(false)

  console.log(info.avatar)
  return <div
    className="card"
    onTouchStart={() => {
      setIsPressed(true)
    }}
    onMouseDown={() => {
      setIsPressed(true)
    }}
    onMouseUp={() => {
      setIsPressed(false)
    }}
    onTouchEnd={() => {
      setIsPressed(false)
    }}
  >
    {getCurrentUserRole() === 'Manager' &&
      <div >
        <img
          src={info.avatar}
          style={{
            top: '-20px',
            left: '-0px',
            position: 'absolute',
            minWidth: '100%',
            width: 'auto',
            borderRadius: '10px'
          }}
        >
        </img>
      </div>
    }
    {isPressed && <Action direction={counterTinder % 3 === 0 ? 'right' : 'left'} />}
    {getCurrentUserRole() !== 'Manager'
      && <div>
        <div className="card-header">
          <div
            className="e-label"
          >
            <SingleTermLabel
              entityType={info.type}
              text={info.type.toUpperCase()}
            />
          </div>
          <h3>{info.name}</h3>
        </div>
        <div className="cut-text">{info.description}</div>
        <div style={{ alignSelf: 'flex-end', marginTop: '48px' }}>
          <div style={{ margin: 4, display: 'flex', flexGrow: 1, alignItems: 'center' }}>
            <img src={info.assigneePic} height={42} width={42} />
            <span style={{ marginLeft: 8, fontWeight: 'bold', flexGrow: 1 }}>{info.asignee}</span>
            <div style={{ fontSize: 'small' }}>
              Effort: {info.effort}
            </div>
          </div>
          <div style={{ marginTop: 4, marginBottom: 4, display: 'flex', flexGrow: 1, alignItems: 'center' }}>
            <Home style={{ marginRight: 4 }} /> {info.iterationName}
          </div>
          <div style={{ marginTop: 4, marginBottom: 4, display: 'flex', flexGrow: 1, alignItems: 'center' }}>
            <LocationOn style={{ marginRight: 4 }} /> Lead time: {info.leadTime}
          </div>
        </div>
      </div>
    }
  </div >
}
