import { CardInfo, CardsProvider } from ".";
import { Client } from "../clients/tp";

export interface AssignableInfo extends CardInfo {
  name: string
  description: string
  state: string,
  type: 'UserStory' | 'Bug' | 'Feature',
  asignee: string,
  assigneePic: string,
  iterationName: string,
  leadTime: string,
  effort: number
}

export const validEffort = [1, 2, 3, 5, 8, 13, 21]
export const validIterations = ['Iteration 1', 'Iteration 2', 'Iteration 3']
export const validTypes = ['UserStory' , 'Bug' , 'Feature']
export const validAsignees = [{
  asignee: 'Roman Kozachenko', assigneePic: 'https://ca.slack-edge.com/ETDMA4DGC-U01NF4WNQF5-940190b3e3de-512'
},{assigneePic: 'https://ca.slack-edge.com/ETDMA4DGC-U032NEX7Q92-174c5e71448a-512', asignee: 'Grzegorz Hawliczek'}, {asignee: 'Aliaksandr Evsuchenya',
  assigneePic: 'https://ca.slack-edge.com/ETDMA4DGC-U01N72AP18W-56912122d62f-512'}]

export function randomIntFromInterval(min: number, max: number) { // min and max included
  return Math.floor(Math.random() * (max - min + 1) + min)
}

const data: AssignableInfo = {
  name: 'Improve Targetprocess performance 1000x',
  description: `Context:
  As our e-commerce platform continues to grow, we have noticed that our NodeJS server is struggling to handle the increased load. We have identified certain API endpoints that are taking longer than expected to respond, causing delays and frustration for our customers.

  Decision:
  After researching various solutions, we have decided to implement caching in our NodeJS server. We will use the popular caching library, Redis, to cache frequently accessed data, such as product information and user sessions. This will reduce the number of requests made to our database, resulting in faster response times and improved performance.

  Rationale:
  Caching is a widely accepted practice for improving the performance of web applications, and Redis is a proven solution for implementing caching in NodeJS. By caching frequently accessed data, we can reduce the load on our database, which will result in faster response times and a better user experience. Additionally, Redis provides built-in support for expiring cached data, which will ensure that we are always serving the most up-to-date information.

  Consequences:
  Implementing caching using Redis will require some changes to our existing codebase. We will need to modify our database queries to check the cache before querying the database, and we will need to ensure that cached data is kept up-to-date. Additionally, we will need to monitor the Redis cache to ensure that it is properly configured and is not causing any unexpected issues. However, we believe that the benefits of implementing caching will outweigh these challenges, and will ultimately result in a faster and more reliable NodeJS server.`,
  state: 'New',
  type: 'UserStory',
  asignee: 'Aliaksandr Evsuchenya',
  assigneePic: 'https://ca.slack-edge.com/ETDMA4DGC-U01N72AP18W-56912122d62f-512',
  iterationName: 'Iteration 1',
  leadTime: '18 days',
  effort: 0
}

export class AssignableCards implements CardsProvider {
  constructor(private client: Client) {}

  public getCards(): CardInfo[] {
    return [...Array(15)].map(_ => ({
      ...data,
      type: validTypes[randomIntFromInterval(0, validTypes.length - 1)],
      ...(validAsignees[randomIntFromInterval(0, validAsignees.length - 1)]),
      effort: validEffort[randomIntFromInterval(0, validEffort.length - 1)],
      iteration: validIterations[randomIntFromInterval(0, validIterations.length - 1)],
      leadTime: `${randomIntFromInterval(1, 10)} days`
    }))
  }
}
